body {
    font-family: Arial, Tahoma;
    font-size: 14px;
    color: #000000;
    margin: 0px;
    padding: 0px;
    line-height: 24px;
    overflow: hidden;
}

.positivesmall, .addsmall, .negativesmall {
    color: #529214;
    border-color: #6DA827;
    font-weight: bold;
    padding-left: 25px;
}

.positivesmall {
    font-size: 20px;
    background: #5CCD00;
    background: -moz-linear-gradient(top, #5CCD00 0%, #4AA400 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #5CCD00), color-stop(100%, #4AA400));
    background: -webkit-linear-gradient(top, #5CCD00 0%, #4AA400 100%);
    background: -o-linear-gradient(top, #5CCD00 0%, #4AA400 100%);
    background: -ms-linear-gradient(top, #5CCD00 0%, #4AA400 100%);
    background: linear-gradient(top, #5CCD00 0%, #4AA400 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#5CCD00', endColorstr='#4AA400', GradientType=0);
    -ms-filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#5CCD00, endColorstr=#4AA400);
    padding: 0px 9px;
    color: #ffffff;
    /*font-family:'Helvetica Neue',sans-serif;*/
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border: 1px solid #0f9900;
    text-decoration: none;
    margin-left: 5px;
}

.positivesmall:hover {
    background: -moz-linear-gradient(top, #4aa400 0%, #5CCD00 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #4aa400), color-stop(100%, #5CCD00));
    background: -webkit-linear-gradient(top, #4aa400 0%, #5CCD00 100%);
    background: -o-linear-gradient(top, #4aa400 0%, #5CCD00 100%);
    background: -ms-linear-gradient(top, #4aa400 0%, #5CCD00 100%);
    background: linear-gradient(top, #4aa400 0%, #5CCD00 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4aa400', endColorstr='#5CCD00', GradientType=0);
    -ms-filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4aa400, endColorstr=#5CCD00);
}

.negativesmall {
    font-size: 20px;
    background: #cc3600;
    background: -moz-linear-gradient(top, #cc3600 0%, #a34c00 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #cc3600), color-stop(100%, #a34c00));
    background: -webkit-linear-gradient(top, #cc3600 0%, #a34c00 100%);
    background: -o-linear-gradient(top, #cc3600 0%, #a34c00 100%);
    background: -ms-linear-gradient(top, #cc3600 0%, #a34c00 100%);
    background: linear-gradient(top, #cc3600 0%, #a34c00 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#cc3600', endColorstr='#a34c00', GradientType=0);
    -ms-filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#cc3600, endColorstr=#a34c00);
    padding: 0px 9px;
    color: #ffffff;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border: 1px solid #992900;
    text-decoration: none;
    margin-left: 5px;
}

.negativesmall:hover {
    background: -moz-linear-gradient(top, #a34c00 0%, #cc3600 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #a34c00), color-stop(100%, #cc3600));
    background: -webkit-linear-gradient(top, #a34c00 0%, #cc3600 100%);
    background: -o-linear-gradient(top, #a34c00 0%, #cc3600 100%);
    background: -ms-linear-gradient(top, #a34c00 0%, #cc3600 100%);
    background: linear-gradient(top, #a34c00 0%, #cc3600 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a34c00', endColorstr='#cc3600', GradientType=0);
    -ms-filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#a34c00, endColorstr=#cc3600);
}


#levelDown:hover,#puzzleDown:hover, #changeTime:hover{
    background: #cdab00;
    background: -moz-linear-gradient(top, #cdab00 0%, #cdab00 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #cdab00), color-stop(100%, #cdab00));
    background: -webkit-linear-gradient(top, #cdab00 0%, #cdab00 100%);
    background: -o-linear-gradient(top, #cdab00 0%, #cdab00 100%);
    background: -ms-linear-gradient(top, #cdab00 0%, #cdab00 100%);
    background: linear-gradient(top, #cdab00 0%, #cdab00 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#5CCD00', endColorstr='#4AA400', GradientType=0);
    -ms-filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#cdab00, endColorstr=#cdab00);

}

#levelDown, #puzzleDown, #changeTime {
    font-size: 20px;
    background: -moz-linear-gradient(top, #ffb700 0%, #ffb700 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #ffb700), color-stop(100%, #ffb700));
    background: -webkit-linear-gradient(top, #ffb700 0%, #ffb700 100%);
    background: -o-linear-gradient(top, #ffb700 0%, #ffb700 100%);
    background: -ms-linear-gradient(top, #ffb700 0%, #ffb700 100%);
    background: linear-gradient(top, #ffb700 0%, #ffb700 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a34c00', endColorstr='#cc3600', GradientType=0);
    -ms-filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#ffb700, endColorstr=#ffb700);
    padding: 0px 9px;
    color: #ffffff;
    /*font-family:'Helvetica Neue',sans-serif;*/
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border: 1px solid #cdab00;
    text-decoration: none;
    margin-left: 5px;
}

#defaultButtons {
    right: 0;
}

#actionButtons {
    text-align: center;
    position: absolute;
    top: 89%;
    left: 50%;
    transform: translateX(-50%);
}

#confirmButton, #rejectButton, #middleButton{
    visibility: hidden;
    font-size: 20px;
}

.x-scroll-disabled {
    overflow-x: hidden;
}

.levelDown {
    text-align: center;
}
#scoreTime{
    position: absolute;
    left: 20px;
    font-size: large;
}

#img3D{
    width: 90%;
    height: 90%;
}

#message{
    font-size: xx-large;
}
.center{
    display: flex;
    align-items: center;
    justify-content: center;
}

#setTime, #timeSetting{
    font-size: 30px;
}
#minus,#plus, #confirm{
    padding: 15px;
    margin-left: 30px;
    margin-right: 30px;
    cursor: pointer
}
